import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import routes from './routes';
import BUILD_VERSION from './build/version';

Vue.use(VueRouter)
Vue.config.productionTip = false
Vue.prototype.$store = {
  devmode: true,
  prevUrl: null,
  historyCmd: [],
};

new Vue({
  render: h => h(App),
  router: new VueRouter({
    base: '/',
    mode: 'history',
    routes,
  })
}).$mount('#app')

const body = document.body;

Array.prototype.last = function () {
  if (this.length === 0) return this[0];
  return this[this.length - 1];
}

window.onresize = () => {
  const screenSize = body.getBoundingClientRect();
  const el = document.getElementById('pc');

  if (!el) return;

  const scale = Math.min (
    (screenSize.width - 100) / el.clientWidth,
    (screenSize.height - 20) / el.clientHeight
  )

  el.style.transform = "matrix(" + scale + ",0,0," + scale + ", 1, 1)";
}

function setSongSettings (){
  const song = document.getElementById("audio");

  song.volume = 0.5;
  song.pause();
}

window.onresize();
setSongSettings();

