const gainSnake = () => {
    if (!Ã¸.snake.gains) return;
    while (Ã¸.snake.gains > 0) {
        if (Ã¸.snake.youth >= Ã¸.food.onlyHealThreshold)
            Ã¸.snake.body.raise();
        Ã¸.snake.youth = 1;
        Ã¸.snake.gains--;
    }
    _.scoreboard.snakeLength = Ã¸.snake.body.length - 2 + Ã¸.snake.youth;
    checkMaxSnakeLength();
}
/** decreasing of snake length */
const getOldSnake = (() => {
    if (DEBUG && !DEBUG.SHOULD_DIE_SNAKE)
        return () => {};

    let dieTimingOverride = DEBUG && DEBUG.OVERRIDE_DIE_INTERVAL
        ? DEBUG.OVERRIDE_DIE_INTERVAL
        : false;
    let lastAging = +new Date;
    return () => {
        const now = +new Date;
        const dieAfter = dieTimingOverride || _.timings.snakeDieAfter;
        if (lastAging + (dieAfter * .1) > now) return;
        lastAging = now;
        let youth = Ã¸.snake.youth;
        if (youth >= .1)
            youth -= .1;
        else
            youth = 0;
        Ã¸.snake.youth = Math.floor(10 * youth) * .1;
        _.scoreboard.snakeLength = Ã¸.snake.body.length - 2 + youth;
        checkMaxSnakeLength();
    }
})();
/** remove part of the snake when it collides with itself */
const killSnakePart = (bodyIndex) => {
    const corpseAdd = Ã¸.snake.body
        .splice(bodyIndex)
        .map(i => ({ ...i, alpha: 2 }));
    Array.prototype.push.apply(
        Ã¸.snake.corpse,
        corpseAdd
        );
    Ã¸.snake.corpse.shift();
    _.scoreboard.snakeLength = Ã¸.snake.body.length - 2 + Ã¸.snake.youth;
    if (corpseAdd.length > 2)
        _.gameEvents.whenColliedItself();
}
