// #region MOVING
const moveSnake = () => {
    const s = Ã¸.snake;
    const b = $.bounds;
    const v = s.velocity;
    const h = s.body.head();

    let x = h.x + v.x;
    if (x > (b.w - 1)) x = 0;
    else if (x < 0) x = b.w - 1;

    let y = h.y + v.y;
    if (y > (b.h - 1)) y = 0;
    else if (y < 0) y = b.h - 1;

    const newHead = { x, y };
    s.body.add(newHead);
    s.body.removeLast();
    const newAss = s.body.ass();

    if (s.youth < 0.1) {
        if (s.body.length === 2) {
            gameShouldOver();
            return;
        }
        s.body.removeLast();
        s.youth = 1;
    }

    const intersectionIndex = s.body.findIndex(c =>
        c !== newHead && c !== newAss
        && c.x === x && c.y === y);
    if (intersectionIndex > -1)
        killSnakePart(intersectionIndex);
};
const moveFood = (directions) => {
    const { pos } = Ã¸.food
    const b = $.bounds;
    Ã¸.food.prev = { ...pos };
    for (const d of directions) {
        switch (d) {
            case 'left': pos.x--; break;
            case 'right': pos.x++; break;
            case 'top': pos.y--; break;
            case 'bottom': pos.y++; break;
        }
    }
    if (pos.x < 0) pos.x = b.w - 2;
    if (pos.y < 0) pos.y = b.h - 2;
    if (pos.x > (b.w - 2)) pos.x = 0;
    if (pos.y > (b.h - 2)) pos.y = 0;
    clearPrevFood();
    drawFood();
}
let clearPrevFood = () => {}
